.main .project1 {
    text-align: center;
    font-size: 20px;
}

.main .project1 h2 {
    padding-bottom: 20px;
}

.main .project1 form {
    display: flex;
    flex-direction: column;
    border-bottom: 2px solid #fff;
}

.main .project1 input {
    margin: 10px 10px;
    font-size: 20px;
}

.main .project1 button {
    margin: 20px auto;
    padding: 10px 30px;
    background-color: rgba(0, 0, 0, .5);
    border: 3px solid #000;
    color: #fff;
    text-transform: uppercase;
}

.main .project1 ul {
    list-style: none;
}

.main .project1 li {
    padding: 10px 0;
}

.main .project1 .taskName {
    font-weight: bold;
    text-transform: uppercase;
}

.main .project1 .taskDate {
    font-size: 15px;
}

.main .project1 .taskID {
    font-size: 11px;
}

.main .project1 .listOfTasks {
    display: flex;
    padding: 20px;
}

.main .project1 .activeTasks {
    flex-basis: 50%;
}

.main .project1 .doneTasks {
    flex-basis: 50%;
}